.answers li {
  background-color: #fff;
  border: 2px solid #0094da;
  min-height: 70px;
  width: 48%;
  display: flex;
  margin-bottom: 30px;
  -webkit-transition: color .3s ease, border-color .3s ease, -webkit-transform .3s ease;
  transition: color .3s ease, border-color .3s ease, -webkit-transform .3s ease;
  transition: color .3s ease, border-color .3s ease, transform .3s ease;
  transition: color .3s ease, border-color .3s ease, transform .3s ease, -webkit-transform .3s ease;
  cursor: pointer; }
  .answers li span {
    background-color: #0084da;
    color: #fff;
    font-size: 30px;
    flex: 75px 1;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transition: background-color .3s ease;
    transition: background-color .3s ease; }
  .answers li p {
    color: #0b0e21;
    font-size: 16px;
    font-weight: 600;
    flex: calc(100% - 75px) 1;
    margin: auto 20px;
    -webkit-transition: color .3s ease;
    transition: color .3s ease; }
  .answers li:after {
    display: block; }
  .answers li:hover {
    -webkit-transform: scale(1.03);
            transform: scale(1.03); }

.submit {
  text-align: center;
